.your-words {
  background-color: $colorOrange;
  color: $colorPurple;

  .section-title {
    h2 {
      font-family: $font-family-default;
      background-color: $colorPurple;
      color: $colorYellow;
      font-size: $font-small;
      font-weight: bold;
      text-transform: capitalize;
    }
  }

  h3 {
    font-family: $font-family-zathu;
    text-transform: uppercase;
    font-size: $font-x-large;
    font-weight: normal;
  }

  input[type=submit], .button {
    background: $colorPurple;
    color: $colorYellow;
    font-size: $font-large;
  }

  form {
    label {
      color: $colorPurple;
      font-size: $font-x-medium!important;
    }

    input {
      &[type=text]{
        padding: 10px 0;
      }

      &[type=checkbox] {
        float: left;
        padding: 0 10px;

        +label {
          line-height: 23px;
          display: block;
          margin-left: 25px;
        }

        &:checked {
          background: $colorWhite url(data:image/png;base64,) no-repeat 50%/90%;
        }
      }
    }
  }
}
