.gem-polls {
  background-color: $colorPurple;

  .list-header {
    background-color: $colorYellow;
    font-size: 16px;
    font-family: $font-family-default;
    color: $colorPurple;
    text-transform: capitalize;
    font-weight: bold;
    padding: 0 16px;
    display: inline-block;
  }

  .list-inline {
    h3 {
      font-family: $font-family-zathu;
      color: $colorYellow;
      font-size: $font-x-large;
      text-transform: uppercase;
      text-align: center;
      font-weight: normal;
    }

    p, label {
      font-family: $font-family-default;
      color: $colorYellow;
      font-size: 18px;
      text-transform: capitalize;
    }

    a, input[type="submit"] {
      color: $colorPurple;
      background: $colorYellow;
    }

    input {
      margin-bottom: 10px;
    }

    .choices p {
      border-top: 1px $colorYellow dashed;;
    }

     .choices {
      border-bottom: 1px $colorYellow dashed;;
    }

    form {
      div div {
        border-top: 1px $colorYellow dashed;
      }

      div:last-child {
        border: none;
        margin: 0;
        text-align: center;
      }

      input[type="radio"], input[type="checkbox"] {
        background: $colorYellow;
        border-radius: 0;
      }

      input[type="radio"]:checked, input[type="checkbox"]:checked {
        background: $colorYellow url(data:image/png;base64,) no-repeat 50%/90%;
      }
    }
  }
}
